import React, { useState, useEffect } from "react";
import axios from "axios";

const Distinction = () => {
    const [classifications, setClassifications] = useState({
        firstClass: 0,
        secondClass: 0,
        distinction: 0
    });

    useEffect(() => {
        const fetchClassifications = async () => {
            try {
                const response = await axios.get("http://localhost:8000/abc/findclassifications");
                setClassifications(response.data);
            } catch (error) {
                console.error("Error fetching classifications:", error);
            }
        };

        fetchClassifications();
    }, []);

    return (
        <div style={{ margin: '20px' }}>
            <h2 style={{ color: 'blue' }}>Classifications</h2>
            <table style={{ borderCollapse: 'collapse', width: '100%' }}>
                <thead>
                    <tr>
                        <th style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>Class</th>
                        <th style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>Total Students</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>First Class</td>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>{classifications.firstClass}</td>
                    </tr>
                    <tr>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>Second Class</td>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>{classifications.secondClass}</td>
                    </tr>
                    <tr>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>Distinction</td>
                        <td style={{ border: '1px solid #ddd', padding: '8px', textAlign: 'left' }}>{classifications.distinction}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

export default Distinction;
