import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import axios from 'axios';

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginError, setLoginError] = useState('');
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    const handleSubmit = (event) => {
        event.preventDefault();

        // Handle login logic here (e.g., send API request)
        axios.post('http://localhost:8000/abc/login', { email, password })
            .then(response => {
                // Handle the successful login
                console.log('Login successful', response.data);

                // Set the state to indicate successful login
                setIsLoggedIn(true);
            })
            .catch(error => {
                // Handle login failure, e.g., show an error message
                console.error('Login failed', error);
                setLoginError('Invalid email or password'); // Set an error message
            });
    };

    // If logged in, render a Link to the "Home" page
    if (isLoggedIn) {
        return <Link to="/Home" />;
    }

    return (
        <div style={{
            backgroundImage: 'url("your-background-image-url")', // Replace with your image URL
            backgroundSize: 'cover',
            height: '100vh',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
        }}>
            <div style={{
                backgroundColor: 'rgba(255, 255, 255, 0.8)',
                padding: '20px',
                borderRadius: '10px',
                boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
                maxWidth: '400px',
                width: '100%',
                margin: '0 auto',
            }}>
                <Form onSubmit={handleSubmit}>
                    <Form.Group controlId='email'>
                        <Form.Label>Email</Form.Label>
                        <Form.Control
                            type='email'
                            placeholder='Enter email'
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            style={{ marginBottom: '15px' }}
                        />
                    </Form.Group>
                    <Form.Group controlId='password'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            type='password'
                            placeholder='Enter password'
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            style={{ marginBottom: '15px' }}
                        />
                    </Form.Group>
                    {loginError && <div style={{ color: 'red', marginBottom: '15px' }}>{loginError}</div>}
                    <Button variant='primary' type='submit' style={{ width: '100%' }}>
                        Login
                    </Button>
                    <div style={{ marginTop: '15px', textAlign: 'center' }}>
                        <Link to="/Home" style={{ textDecoration: 'underline', color: 'blue' }}>
                            Forgot password
                        </Link> <Link to="/Registration" style={{ textDecoration: 'underline', color: 'blue' }}>
                            create acount
                        </Link>
                    </div>
                </Form>
            </div>
        </div>
    );
};

export default Login;
