import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import axios from 'axios';

const Addstudent = () => {
  // State to hold form data
  const [formData, setFormData] = useState({
    rollno: '',
    fullName: '',
    physicsMarks: '',
    chemistryMarks: '',
    mathsMarks: '',
    biologyMarks: '',
    totalMarks: '',
  });

  // Handle form field changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Send data to MongoDB server
      await axios.post('http://localhost:8000/abc/addstudentmarks', formData);
      // Clear form after successful submission
      setFormData({
        rollno: '',
        fullName: '',
        physicsMarks: '',
        chemistryMarks: '',
        mathsMarks: '',
        biologyMarks: '',
        totalMarks: '',
      });
      console.log('Form data submitted successfully!');
    } catch (error) {
      console.error('Error submitting form data:', error);
    }
  };

  return (
    <Form onSubmit={handleSubmit} style={{
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      backgroundColor: '#f5f5f5',
      padding: '30px',
      borderRadius: '40px',
      boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.1)',
      width: '400px',
      margin: '0 auto',
      marginTop: '20px'
    }}>
      <Form.Group controlId="rollno">
        <Form.Label>Roll No</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Roll No"
          name="rollno"
          value={formData.rollno}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="fullName">
        <Form.Label>Full Name</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Full Name"
          name="fullName"
          value={formData.fullName}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="physicsMarks">
        <Form.Label>Physics Marks</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Physics Marks"
          name="physicsMarks"
          value={formData.physicsMarks}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="chemistryMarks">
        <Form.Label>Chemistry Marks</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Chemistry Marks"
          name="chemistryMarks"
          value={formData.chemistryMarks}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="mathsMarks">
        <Form.Label>Maths Marks</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Maths Marks"
          name="mathsMarks"
          value={formData.mathsMarks}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="biologyMarks">
        <Form.Label>Biology Marks</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Biology Marks"
          name="biologyMarks"
          value={formData.biologyMarks}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Form.Group controlId="totalMarks">
        <Form.Label>Total Marks</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Total Marks"
          name="totalMarks"
          value={formData.totalMarks}
          onChange={handleInputChange}
          required
          style={{
            width: '100%',
            padding: '10px',
            border: '1px solid #ccc',
            borderRadius: '3px',
            boxSizing: 'border-box',
            marginBottom: '10px',
            fontSize: '16px'
          }}
        />
      </Form.Group>

      <Button variant="primary" type="submit" style={{
        alignSelf: 'center',
        backgroundColor: '#4CAF50',
        marginTop: '10px',
        color: 'white',
        padding: '10px 20px',
        border: 'none',
        borderRadius: '3px',
        cursor: 'pointer',
        fontSize: '16px',
        textAlign: 'center'
      }}>
        Submit
      </Button>
    </Form>
  );
};

export default Addstudent;
