import React, { useState, useEffect } from "react";
import axios from "axios";
import Distinction from "./Distinction";

function Subjectpass() {
  const [data, setData] = useState([]);

  useEffect(() => {
    const url = "http://localhost:8000/abc/subjectwiseanalysis";

    axios
      .get(url)
      .then((response) => {
        setData(response.data);
      })
      .catch((error) => {
        console.error(error);
      });
  }, []);

  return (
    <div style={{
      textAlign: 'center',
      backgroundColor: '#f5f5f5',
      padding: '30px',
      borderRadius: '40px',
      boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.1)',
      width: '80%',
      margin: '0 auto',
      marginTop: '20px'
    }}>
      <h2>Subject-wise Analysis</h2>
      <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '20px' }}>
        <thead>
          <tr style={tableHeaderStyle}>
            <th>Physics</th>
            <th>Chemistry</th>
            <th>Maths</th>
            <th>Biology</th>
          </tr>
        </thead>
        <tbody>
          <tr style={tableCellStyle}>
            <td>Pass: {data?.analysis?.pass?.physicsMarks || 0}</td>
            <td>Pass: {data?.analysis?.pass?.chemistryMarks || 0}</td>
            <td>Pass: {data?.analysis?.pass?.mathsMarks || 0}</td>
            <td>Pass: {data?.analysis?.pass?.biologyMarks || 0}</td>
          </tr>
          <tr style={tableCellStyle}>
            <td>Fail: {data?.analysis?.fail?.physicsMarks || 0}</td>
            <td>Fail: {data?.analysis?.fail?.chemistryMarks || 0}</td>
            <td>Fail: {data?.analysis?.fail?.mathsMarks || 0}</td>
            <td>Fail: {data?.analysis?.fail?.biologyMarks || 0}</td>
          </tr>
          <tr style={tableCellStyle}>
            <td>Absent: {data?.analysis?.absent?.physicsMarks || 0}</td>
            <td>Absent: {data?.analysis?.absent?.chemistryMarks || 0}</td>
            <td>Absent: {data?.analysis?.absent?.mathsMarks || 0}</td>
            <td>Absent: {data?.analysis?.absent?.biologyMarks || 0}</td>
          </tr>
        </tbody>
      </table>
      <Distinction />
    </div>
  );
}

const tableHeaderStyle = {
  backgroundColor: '#4CAF50',
  color: 'white',
  padding: '12px',
  border: '1px solid #ddd',
};

const tableCellStyle = {
  padding: '8px',
  border: '1px solid #ddd',
};

export default Subjectpass;
