import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import image1 from "../images/corosol1.jpg";
import image2 from "../images/corosol2.jpg";
import image5 from "../images/corosol5.jpg";
import chemical from "../images/chemical.jpg"
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { Container } from 'react-bootstrap';
import { useState } from 'react';



function IndividualIntervalsExample() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });

  // Handle form field changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    // Add your logic for form submission (e.g., sending data to a server)
    console.log('Form submitted:', formData);
  };

  // Form fields configuration
  const formFields = [
    { name: 'name', label: 'Name', type: 'text' },
    { name: 'email', label: 'Email', type: 'email' },
    { name: 'message', label: 'Message', type: 'textarea' },
  ];

  return (
    <>
      <div>
        <main>
          <div id="myCarousel" className="carousel slide mb-6" data-bs-ride="carousel">
            <div className="carousel-indicators">
              <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="0" aria-label="Slide 1"></button>
              <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="1" aria-label="Slide 2" className="active" aria-current="true"></button>
              <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="2" aria-label="Slide 3"></button>
            </div>
            <div className="carousel-inner">
              <div className="carousel-item">
                <img
                  className="d-block w-100"
                  src={image1}
                  alt="First slide"
                  height={600}
                  width={1600}
                />
                <div className="container">
                  <div className="carousel-caption text-start">
                    <h1>Example headline.</h1>
                    <p className="opacity-75">Some representative placeholder content for the first slide of the carousel.</p>
                    <p><a className="btn btn-lg btn-primary" href="#">Sign up today</a></p>
                  </div>
                </div>
              </div>
              <div className="carousel-item active">
                <img
                  className="d-block w-100"
                  src={image2}
                  alt="Second slide"
                  height={600}
                  width={1600}
                />
                <div className="container">
                  <div className="carousel-caption">
                    <h1>Another example headline.</h1>
                    <p>Some representative placeholder content for the second slide of the carousel.</p>
                    <p><a className="btn btn-lg btn-primary" href="#">Learn more</a></p>
                  </div>
                </div>
              </div>
              <div className="carousel-item">
                <img
                  className="d-block w-100"
                  src={image5}
                  alt="Third slide"
                  height={600}
                  width={1600}
                />
                <div className="container">
                  <div className="carousel-caption text-end">
                    <h1>One more for good measure.</h1>
                    <p>Some representative placeholder content for the third slide of this carousel.</p>
                    <p><a className="btn btn-lg btn-primary" href="#">Browse gallery</a></p>
                  </div>
                </div>
              </div>
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
              <span className="carousel-control-prev-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Next</span>
            </button>
          </div>


        </main>
      </div>
      <hr />

      <div>
        <section id="about" class="about">
          <div class="container" data-aos="fade-up">

            <div class="section-title">
              <h2>----------------------------------------------------  About Us  ------------------------------------------------</h2>
            </div>

            <div class="row content">
              <div class="col-lg-6">
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore
                  magna aliqua.
                </p>
                <ul>
                  <li><i class="ri-check-double-line"></i> Ullamco laboris nisi ut aliquip ex ea commodo consequat</li>
                  <li><i class="ri-check-double-line"></i> Duis aute irure dolor in reprehenderit in voluptate velit</li>
                  <li><i class="ri-check-double-line"></i> Ullamco laboris nisi ut aliquip ex ea commodo consequat</li>
                </ul>
              </div>
              <div class="col-lg-6 pt-4 pt-lg-0">
                <p>
                  Ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate
                  velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
                  culpa qui officia deserunt mollit anim id est laborum.
                </p>
                <a href="#" class="btn-learn-more">Learn More</a>
              </div>
            </div>

          </div>
        </section>
      </div>
      <hr />
      <div>
        <div style={{ maxWidth: '600px', margin: '0 auto' }}>
          <h2>-------------------- Contact Us -------------------</h2>
          <form onSubmit={handleSubmit} style={{ display: 'grid', gap: '10px' }}>
            {formFields.map((field) => (
              <div key={field.name}>
                <label style={{ display: 'block', marginBottom: '5px' }}>{field.label}</label>
                {field.type === 'textarea' ? (
                  <textarea
                    name={field.name}
                    value={formData[field.name]}
                    onChange={handleInputChange}
                    style={{ width: '100%', padding: '8px', borderRadius: '4px' }}
                    required
                  />
                ) : (
                  <input
                    type={field.type}
                    name={field.name}
                    value={formData[field.name]}
                    onChange={handleInputChange}
                    style={{ width: '100%', padding: '8px', borderRadius: '4px' }}
                    required
                  />
                )}
              </div>
            ))}
            <button type="submit" style={{ padding: '10px', backgroundColor: '#007bff', color: 'white', borderRadius: '4px' }}>
              Submit
            </button>
          </form>
        </div>
      </div>

    </>
  );
}


export default IndividualIntervalsExample;
