import React, { useState, useEffect } from "react";
import axios from "axios";

function ClassToppers() {
    const [classToppers, setClassToppers] = useState({});

    useEffect(() => {
        const fetchClassToppers = async () => {
            try {
                // Make a request to your backend to fetch class toppers
                const response = await axios.get("http://localhost:8000/abc/findClassToppers");

                // Update the state with the received data
                setClassToppers(response.data.classToppers);
            } catch (error) {
                console.error("Error fetching class toppers:", error);
            }
        };

        // Call the fetchClassToppers function when the component mounts
        fetchClassToppers();
    }, []);

    return (
        <div style={{ marginTop: '20px', textAlign: 'center' }}>
            <hr />
            <h3>Class Topper</h3>
            <table style={{ margin: 'auto' }}>
                <thead>
                    <tr>
                        <th style={{ padding: '10px' }}>Roll Number</th>
                        <th style={{ padding: '10px' }}>Full Name</th>
                        <th style={{ padding: '10px' }}>Marks</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td style={{ padding: '10px' }}>{classToppers?.classTopper?.rollno}</td>
                        <td style={{ padding: '10px' }}>{classToppers?.classTopper?.fullName}</td>
                        <td style={{ padding: '10px' }}>{classToppers?.classTopper?.marks}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

export default ClassToppers;
