import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

function Updatelogin() {
    const [validated, setValidated] = useState(false);
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");
    const [email, setEmail] = useState("");
    const [cPassword, setCPassword] = useState("");

    // Retrieve email from local storage
    const { userEmail } = useParams();
    const storedEmail = localStorage.getItem("email");

    useEffect(() => {
        // Check if storedEmail is available, otherwise use the one from the URL
        const emailToFetch = storedEmail || userEmail;

        axios.get(`http://localhost:8000/abc/findone/${emailToFetch}`)
            .then(res => {
                const userData = res.data.userdata;
                setName(userData.name);
                setEmail(userData.email);
                setPassword(userData.password);
                setCPassword(userData.cpassword);
            })
            .catch(err => {
                console.log(err);
            });
    }, [userEmail, storedEmail]);

    const handleSubmit = (event) => {
        event.preventDefault();

        const updatedData = {
            name: name,
            password: password,
            email: email,
            cpassword: cPassword
        };

        axios.put(`http://localhost:8000/abc/update/${email}`, updatedData)
            .then(res => {
                console.log(res.data);
            })
            .catch(err => {
                console.log(err);
            });

        setValidated(true);
    };

    return (
        <Form noValidate validated={validated} onSubmit={handleSubmit} style={{
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: '#f5f5f5',
            padding: '30px',
            borderRadius: '40px',
            boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.1)',
            width: '400px',
            margin: '0 auto',
            marginTop: '20px'
        }}>
            <Form.Group className="mb-3" controlId="formBasicname">
                <Form.Label>Name</Form.Label>
                <Form.Control
                    type="text"
                    placeholder="Name"
                    id="name"
                    name="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    style={{
                        width: '100%',
                        padding: '10px',
                        border: '1px solid #ccc',
                        borderRadius: '3px',
                        boxSizing: 'border-box',
                        marginBottom: '10px',
                        fontSize: '16px'
                    }}
                />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                    type="email"
                    placeholder="Enter email"
                    id="email"
                    name="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    style={{
                        width: '100%',
                        padding: '10px',
                        border: '1px solid #ccc',
                        borderRadius: '3px',
                        boxSizing: 'border-box',
                        marginBottom: '10px',
                        fontSize: '16px'
                    }}
                />
                <Form.Text className="text-muted">
                    We'll never share your email with anyone else.
                </Form.Text>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control
                    type="password"
                    placeholder="Password"
                    id="password"
                    name="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    style={{
                        width: '100%',
                        padding: '10px',
                        border: '1px solid #ccc',
                        borderRadius: '3px',
                        boxSizing: 'border-box',
                        marginBottom: '10px',
                        fontSize: '16px'
                    }}
                />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicConfirmPassword">
                <Form.Label>Confirm Password</Form.Label>
                <Form.Control
                    type="password"
                    placeholder="Confirm Password"
                    id="confirmPassword"
                    name="confirmPassword"
                    value={cPassword}
                    onChange={(e) => setCPassword(e.target.value)}
                    required
                    style={{
                        width: '100%',
                        padding: '10px',
                        border: '1px solid #ccc',
                        borderRadius: '3px',
                        boxSizing: 'border-box',
                        marginBottom: '10px',
                        fontSize: '16px'
                    }}
                />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                <Form.Check type="checkbox" label="Check me out" />
            </Form.Group>

            <Button variant="primary" type="submit" style={{
                alignSelf: 'center',
                backgroundColor: '#4CAF50',
                marginLeft: '100px',
                color: 'white',
                padding: '10px 20px',
                border: 'none',
                borderRadius: '3px',
                cursor: 'pointer',
                fontSize: '16px',
                textAlign: 'center'
            }}>
                Submit
            </Button>
        </Form>
    );
}

export default Updatelogin;
