import React from 'react';
import civil from "../images/civil.jpg"
import mechanical from "../images/Mechanical.jpg"
import electrical from "../images/electrical.jpg"
import comp from "../images/computer.jpg"
import chemical from "../images/chemical.jpg"
import aerospace from "../images/Aerospace.jpg"
import biomedical from "../images/biomedical.jpg"
import environmental from "../images/Envm.jpg"
import industrial from "../images/industrial.jpg"
import materials from "../images/materials.jpg"
import petroleum from "../images/petrolem.jpg"
import nuclear from "../images/nuclear.jpg"
import telecommunication from "../images/Telecommunication.jpg"
import marine from "../images/marine.jpg"
import biotechnology from "../images/biotechnology.jpg"
import electronics from "../images/electronics.jpg"
import software from "../images/software.jpg"
import robotics from "../images/robotics.jpg"
import mining from "../images/mining.jpg"
import mechatronics from "../images/mechatronics.jpg"

const departments = [
    { name: 'Civil Engineering', image: civil },
    { name: 'Mechanical Engineering', image: mechanical },
    { name: 'Electrical Engineering', image: electrical },
    { name: 'Computer Science and Engineering', image: comp },
    { name: 'Chemical Engineering', image: chemical },
    { name: 'Aerospace Engineering', image: aerospace },
    { name: 'Biomedical Engineering', image: biomedical },
    { name: 'Environmental Engineering', image: environmental },
    { name: 'Industrial Engineering', image: industrial },
    { name: 'Materials Engineering', image: materials },
    { name: 'Petroleum Engineering', image: petroleum },
    { name: 'Nuclear Engineering', image: nuclear },
    { name: 'Telecommunication Engineering', image: telecommunication },
    { name: 'Marine Engineering', image: marine },
    { name: 'Biotechnology Engineering', image: biotechnology },
    { name: 'Electronics Engineering', image: electronics },
    { name: 'Software Engineering', image: software },
    { name: 'Robotics Engineering', image: robotics },
    { name: 'Mining Engineering', image: mining },
    { name: 'Mechatronics Engineering', image: mechatronics }
];

function DepartmentCard({ name, image }) {
    return (
        <div style={{
            backgroundColor: '#f5f5f5',
            borderRadius: '5px',
            boxShadow: '0 2px 5px rgba(0, 0, 0, 0.1)',
            margin: '10px',
            width: '250px',
            textAlign: 'center',
            padding: '20px',
            transition: 'transform 0.2s ease-in-out',
            cursor: 'pointer',
        }} className="department-card">
            <img src={image} alt={name} style={{
                width: '100%',
                height: '150px',
                objectFit: 'cover',
                borderRadius: '5px',
                marginBottom: '10px',
            }} />
            <h3 style={{
                fontFamily: 'sans-serif',
                fontSize: '18px',
                fontWeight: 'bold',
                margin: '0',
                color: '#333',
            }}>{name}</h3>
        </div>
    );
}

function Departments() {
    return (
        <div style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            margin: '50px 0',
        }} className="departments-container">
            {departments.map((department) => (
                <DepartmentCard key={department.name} {...department} />
            ))}
        </div>
    );
}

export default Departments;
