import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import Updatelogins from "./Updatelogins";

function Admin() {
    const [data, setData] = useState([]);
    const navigate = useNavigate(); // Use useNavigate hook

    // Use useEffect hook to fetch data when the component mounts
    useEffect(() => {
        // Define the API endpoint
        const url = "http://localhost:8000/abc/final";

        // Use axios.get method to make the request
        axios
            .get(url)
            .then((response) => {
                // If the request is successful, update the data state with the response data
                setData(response.data);
            })
            .catch((error) => {
                // If the request fails, log the error to the console
                console.error(error);
            });
    }, []); // Pass an empty dependency array to run the effect only once

    const handleUpdate = (email) => {
        // Use navigate instead of window.location.href
        navigate(`/Updatelogins/${email}`);
        // Store email in local storage
        localStorage.setItem("email", email);
    };

    const handleDelete = (email) => {
        // Perform the delete operation using axios
        axios
            .delete(`http://localhost:8000/abc/delete/${email}`)
            .then((response) => {
                // If delete is successful, update the data state without the deleted item
                setData(data.filter(item => item.email !== email));
            })
            .catch((error) => {
                console.error(error);
            });
    };

    // Return the JSX element to render the data
    return (
        <div className="App">
            <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '20px' }}>
                <thead>
                    <tr style={{ background: '#f2f2f2' }}>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Name</th>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Email</th>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Password</th>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Confirm Password</th>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Update</th>
                        <th style={{ padding: '10px', border: '1px solid #dddddd' }}>Delete</th>
                    </tr>
                </thead>
                <tbody>
                    {data.map((b) => (
                        <tr key={b.email}>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>{b.name}</td>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>{b.email}</td>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>{b.password}</td>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>{b.cpassword}</td>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>
                                <button style={{ padding: '5px 10px', background: '#4CAF50', color: 'white', border: 'none', borderRadius: '3px' }} onClick={() => handleUpdate(b.email)}>
                                    Update
                                </button>
                            </td>
                            <td style={{ padding: '10px', border: '1px solid #dddddd' }}>
                                <button style={{ padding: '5px 10px', background: '#f44336', color: 'white', border: 'none', borderRadius: '3px' }} onClick={() => handleDelete(b.email)}>
                                    Delete
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

export default Admin;
