import React, { useState, useEffect } from "react";
import axios from "axios";
import ClassToppers from "./ClassToppers";

function Topper() {
    const [toppers, setToppers] = useState([]);

    useEffect(() => {
        const fetchToppers = async () => {
            try {
                // Make a request to your backend to fetch subject toppers
                const response = await axios.get("http://localhost:8000/abc/findSubjectToppers");

                // Update the state with the received data
                setToppers(response.data.subjectToppers);
            } catch (error) {
                console.error("Error fetching subject toppers:", error);
            }
        };

        // Call the fetchToppers function when the component mounts
        fetchToppers();
    }, []);

    return (
        <div style={{
            textAlign: 'center',
            backgroundColor: '#f5f5f5',
            padding: '30px',
            borderRadius: '40px',
            boxShadow: '0px 2px 5px rgba(0, 0, 0, 0.1)',
            width: '80%',
            margin: '0 auto',
            marginTop: '20px'
        }}>
            <h2>Subject Toppers</h2>
            <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '20px' }}>
                <thead>
                    <tr>
                        <th style={tableHeaderStyle}>Subject</th>
                        <th style={tableHeaderStyle}>Roll Number</th>
                        <th style={tableHeaderStyle}>Full Name</th>
                        <th style={tableHeaderStyle}>Marks</th>
                    </tr>
                </thead>
                <tbody>
                    {Object.keys(toppers).map((subject) => (
                        <tr key={subject}>
                            <td style={tableCellStyle}>{subject}</td>
                            <td style={tableCellStyle}>{toppers[subject].rollno}</td>
                            <td style={tableCellStyle}>{toppers[subject].fullName}</td>
                            <td style={tableCellStyle}>{toppers[subject].marks}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <ClassToppers />
        </div>
    );
}

const tableHeaderStyle = {
    backgroundColor: '#4CAF50',
    color: 'white',
    padding: '12px',
    border: '1px solid #ddd',
};

const tableCellStyle = {
    padding: '8px',
    border: '1px solid #ddd',
};

export default Topper;
